import React from 'react'
import { AppBar, Button, Toolbar, Tooltip } from "@mui/material";
import { Link } from "react-router-dom";
import HomeIcon from '@mui/icons-material/Home';
import SearchIcon from '@mui/icons-material/Search';
import HistoryIcon from '@mui/icons-material/History';
import LoginIcon from '@mui/icons-material/Login';
import PersonIcon from '@mui/icons-material/Person';
import PersonSearchIcon from '@mui/icons-material/PersonSearch';
import ManageAccountsIcon from '@mui/icons-material/ManageAccounts';
import ManageSearchIcon from '@mui/icons-material/ManageSearch';

export const Nav = () => {
    return (
        <AppBar position="fixed">
            <Toolbar>
                <Tooltip title=" " arrow>
                    <Button color="inherit" component={Link} to="/Home">
                        <HomeIcon />
                    </Button>
                </Tooltip>
                <Button color="inherit" component={Link} to="/Manager" >
                                    </Button>
                <Tooltip title=" " arrow>

                    <Button color="inherit" component={Link} to="/Filter">
                        <PersonSearchIcon />
                    </Button>
                </Tooltip>
                <Button color="inherit" component={Link} to="/Secretary">
                    
                </Button>
                <Tooltip title=" " arrow>
                <Button color="inherit" component={Link} to="/EditingFilters">
                    <ManageSearchIcon />
                </Button>
                </Tooltip>
                <Tooltip title=" " arrow>
                    <Button color="inherit" component={Link} to="/WorkersManagement">
                        <ManageAccountsIcon />
                    </Button>
                </Tooltip>
                <Tooltip title=" " arrow>

                    <Button color="inherit" component={Link} to="/History/:id">
                        <HistoryIcon />
                    </Button>
                </Tooltip>
                <Tooltip title="" arrow>

                    <Button color="inherit" component={Link} to="/Profile">
                        <PersonIcon />
                    </Button>
                </Tooltip>
                <Tooltip title="" arrow>

                    <Button color="inherit" component={Link} to="/Login" >
                        <LoginIcon />
                    </Button>
                </Tooltip>
                <Button color="inherit" component={Link} to="/SignUp">
                    
                </Button>
            </Toolbar>
        </AppBar>
    );
}